/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"William Lindner, 2 n's","author":"William Lindner"}},"markdownRemark":{"id":"/Users/william/workspace/gatsby-blog/src/pages/2015-05-01-react-in-your-react-native-webview-so-you-can-react-while-you-react/index.md absPath of file >>> MarkdownRemark","html":"<p>React Native is great. Or, at least, it promises to be great. It was just released, so time will tell how well it works out. But, for a project that’s only been out for a few months, it’s already made a huge splash. If you haven’t heard of it, <a href=\"https://facebook.github.io/react-native/\" target=\"_blank\" rel=\"nofollow noopener noreferrer\">check it out</a>. It could be a good solution for building your next cross platform native app. It allows you to write your app in React’s Component structure, but instead of rendering DOM elements, it renders iOS (and eventually Android) native elements. Instead of <code>&#x3C;div></code>’s and <code>&#x3C;span></code>’s, you’re writing <code>&#x3C;View></code> and <code>&#x3C;Text></code> elements. But, that’s not really what this article is about. For getting started with React Native, I suggest React Native’s <a href=\"http://facebook.github.io/react-native/docs/getting-started.html#content\" target=\"_blank\" rel=\"nofollow noopener noreferrer\">Getting Started Guide</a>.</p>\n<p>This article is about using DOM based React components within a React Native app. It’s kind of a funky idea, but it’s not all that hard to pull off. So, I hope it helps generate some new ideas.</p>\n<p>If you want to skip to the code, check out the complete repo <a href=\"https://github.com/wlindner/ReactNativeReactWebview\" target=\"_blank\" rel=\"nofollow noopener noreferrer\">here</a>.</p>\n<p>This is what it will look like when we’re done.\n<br>\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/react-native-react-webview-end-goal-41da215fd2753c1ea03abccd96518917-c567e.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 263px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 190.11406844106463%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"React Native React Webview End Goal\"\n        title=\"\"\n        src=\"/static/react-native-react-webview-end-goal-41da215fd2753c1ea03abccd96518917-c567e.png\"\n        srcset=\"/static/react-native-react-webview-end-goal-41da215fd2753c1ea03abccd96518917-6a757.png 148w,\n/static/react-native-react-webview-end-goal-41da215fd2753c1ea03abccd96518917-c567e.png 263w\"\n        sizes=\"(max-width: 263px) 100vw, 263px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n<br>\n<em>Not exactly mind blowing stuff right here, but it’s a start.</em></p>\n<ol>\n<li>\n<p>Set up your React Native project using the <a href=\"https://facebook.github.io/react-native/docs/getting-started.html\" target=\"_blank\" rel=\"nofollow noopener noreferrer\">Getting Started Guide</a>. This will mostly consist of installing react-native-cli: <code>npm install -g react-native-cli</code>. Then, initializing your project: <code>react-native init AwesomeProject</code></p>\n</li>\n<li>\n<p>Copy <a href=\"https://github.com/wlindner/ReactNativeReactWebview/tree/master/web\" target=\"_blank\" rel=\"nofollow noopener noreferrer\">this folder called ‘web’</a> of an HTML file and two Javascript files into your Xcode project. It’s just a React Hello World that I got from React’s <a href=\"https://facebook.github.io/react/docs/getting-started.html\" target=\"_blank\" rel=\"nofollow noopener noreferrer\">Getting Started Guide</a>.\n<br>\n<br>\n<img src=\"/9dceefaab705cdec4596d2864154aea2.gif\" alt=\"Copy the web folder into your project\">\n<br>\n<br></p>\n</li>\n<li>\n<p>Modify your index.ios.js file to look like <a href=\"https://github.com/wlindner/ReactNativeReactWebview/blob/master/index.ios.js\" target=\"_blank\" rel=\"nofollow noopener noreferrer\">this</a>. One of the most important bits is this: <code>&#x3C;WebView url='web/helloworld.html' /></code>. It tells React Native’s <code>&#x3C;WebView></code> component to load the HTML file you moved into the project in step 2.  React Native’s webview loads the file using iOS native code without any additional work. It even knows how to load the javascript files included into the page’s <code>&#x3C;head></code>.</p>\n</li>\n<li>\n<p>Now, if you start up your application by running it in XCode, you’ll see a web view running your React Hello World application.</p>\n</li>\n</ol>\n<p>But, wait, you’re asking… “Why are we doing this? I thought I was using React Native instead of React”. And, that’s true. But, the reality is that if you’re using React Native, you’re probably a web developer, and you probably are already familiar with React and you may even have some great React components you want to reuse. Now you have even more flexibility because you can pick between reusing what you’ve already created or using native components.</p>","frontmatter":{"title":"Putting React in your React Native Webview So You Can React While You React","date":"May 01, 2015"}}},"pathContext":{"path":"/react-in-your-react-native-webview/","_PARENT":"SOURCE"}}

/***/ })

